@import "../global/fonts.scss";

.welcome__wrapper {
  display: flex;
  justify-content: center;
  background-size: 100%;
  background-image: url("../img/rectangle-background.svg"),
    url("../img/welcome-back-mobile.jpg");
  background-repeat: no-repeat;
  background-position: bottom center;
  padding: 90px 19px 85px;
  margin-bottom: 32px;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/welcome-back-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    background-image: url("../img/rectangle-tablet.svg"), url("../img/back-desktop.jpg");
    padding: 77px 154px 91px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/back-desktop@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/intro-triangle-desktop.svg"), url("../img/hero-back-desktop.jpg");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/hero-back-desktop@2x.jpg");
    }
  }
}

.welcome__slogan {
  margin: 0;
  margin-bottom: 35px;
  text-align: center;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  padding: 0 18px;
  text-transform: uppercase;
  color: $black;

  @media (min-width: $tablet-width) {
    margin-bottom: 35px;
    font-size: 21px;
    line-height: 26px;
    padding: 0 124px;
  }
}

.welcome__intro {
  margin: 0;
  text-transform: uppercase;
  text-align: center;
  padding: 0 18px;
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    margin-bottom: 35px;
    font-size: 14px;
    line-height: 21px;
    padding: 0 124px;
  }
}

.welcome__image {
  display: block;
  max-width: 100%;
  height: auto;
  object-fit: contain;

  @media (min-width: $tablet-width) {
    width: 460px;
  }
}
